.tabs {
  padding: 41px 20px 32px 40px;
}

.tabs__buttons {
  margin-bottom: 29px;
}

.tabs__btn {
  background-color: #262931;
  border-radius: 10px;
  border: none;
  margin-right: 10px;
  padding: 14px;

  span {
    @include font-settings (500, 14px, 1);
    opacity: 0.5;
  }

  &.active {
    background-color: #FFC600;

    span {
      color: #14161B;
      opacity: 1;
    }
  }
}

.tabs__content {
  display: grid;
  grid-template-columns: 400px 485px;
  gap: 21px;
}

.tabs__content-block {
  background-color: #262931;
  border-radius: 10px;
  padding: 26px 29px;

  &--basic {
    grid-column: 1 / 2;
  }

  &--contacts {
    grid-column: 2 / 3;
  }

  &--signature {
    grid-column: 1 / 3;
  }

  &--stats {
    grid-column: 1 / 3;
  }
}

.tabs__heading {
  display: flex;
}

.tabs__heading h3 {
  @include font-settings (600, 18px, 1);
  margin-right: 5px;
}

.tabs__edit-btn {
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: $color-default-white;
}

.tabs__user-info {
  display: grid;
  grid-template-columns: repeat(2, auto);
  margin: 0;
  margin-top: 30px;
}

.tabs__content-block dt {
  @include font-settings (400, 14px, 1.6);
  opacity: 0.7;
}

.tabs__content-block dd {
  @include font-settings (600, 14px, 1.6);
  text-transform: uppercase;
}

.tabs__contacts-list {
  display: flex;
  margin-top: 21px;
}

.tabs__contacts-item {
  margin-right: 10px;
  background-color: #1E2228;
  border-radius: 10px;
  padding: 7px;
}

.tabs__contacts-link {
  display: flex;
  align-items: center;
  color: $color-accent;

  svg {
    margin-right: 9px;
  }
}

.tabs__content-block--signature p {
  @include font-settings (600, 14px, 1.6);
  text-transform: uppercase;
  margin-top: 15px;
}

.tabs__stats-list {
  display: flex;
  flex-wrap: wrap;
}

.tabs__stats-item {
  margin-right: 50px;
}


.tabs__stats--msgs {
  display: grid;
  grid-template-columns: repeat(2, auto);
}

.tabs__stats {
  dt {
    margin-right: 10px;
  }
  dd {
    margin: 0;
  }
}
