@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Light.woff2") format("woff2"), url("../fonts/Inter-Light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Bold.woff2") format("woff2"), url("../fonts/Inter-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Inter", Arial, sans-serif;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  color: #ffffff;
  background-color: #14161B;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a {
  color: #ffffff;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 100px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  background-color: #1E2228;
}

.header .container {
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
  justify-content: space-between;
}

.header__search {
  position: relative;
}

.header__search input {
  border: 1px solid #2D3341;
  border-radius: 10px;
  background-color: transparent;
  width: 456px;
  height: 40px;
  padding-left: 30px;
  color: #ffffff;
}

.header__search-btn {
  position: absolute;
  background-color: transparent;
  border: none;
  top: 9px;
  left: 0;
}

.user-menu {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  width: 130px;
}

.user-menu a {
  display: block;
  position: relative;
}

.user-menu__alerts-icon {
  position: absolute;
  top: -3px;
  left: 5px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 2px solid #1E2228;
  background-color: #FF0000;
}

.index-main .container,
.block-main .container,
.vipblock-main .container {
  display: grid;
  grid-template-columns: auto 295px;
  column-gap: 20px;
  padding-top: 25px;
  padding-bottom: 25px;
}

.post-main .container,
.userpage-main .container,
.company-main .container {
  padding-top: 25px;
  padding-bottom: 25px;
}

.new {
  margin-bottom: 22px;
}

.new__block {
  padding: 20px;
}

.new__button-wrapper {
  display: flex;
  margin-bottom: 5px;
}

.new__btn {
  font-family: "Inter", Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 1;
  color: #ffffff;
  padding: 14px;
  border: none;
  display: flex;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
  opacity: 0.5;
  cursor: pointer;
}
.new__btn--themes {
  background-color: #303542;
  border-radius: 10px 0 0 10px;
}
.new__btn--comments {
  background-color: #1A1D24;
  border-radius: 0 10px 10px 0;
}

.new__item {
  display: grid;
  grid-template-columns: 587px 134px 121px;
  column-gap: 20px;
  padding-top: 15px;
}
.new__item:not(:last-child) {
  border-bottom: 1px solid #606368;
