  padding-left: 25px;
}
.new__item p::before {
  position: absolute;
  content: url(../../img/svg/clock-icon.svg);
  top: 0;
  left: 0;
}

.block {
  background-color: #262931;
  border-radius: 20px;
}

.block__heading {
  background-color: #1A1D24;
  padding: 10px 12px;
  border-radius: 20px 20px 0 0;
