}

.block__col--2 .block__add-icon {
  fill: #ffffff;
}

.block__col--3 p {
  font-family: "Inter", Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1;
  color: #ffffff;
}
.block__col--3 span {
  font-family: "Inter", Arial, sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 1;
  color: #ffffff;
}

.block__col--4 a {
  font-family: "Inter", Arial, sans-serif;
  font-style: normal;
